#skills{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 0 auto;
}

.skillTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top:3rem;
    margin-bottom: 1.5rem;
    
}

.skillDescription{
    font-size: 1rem;
    font-weight: 400;
    margin: 0 2rem ;
    text-align: start;
}

.skillDescription span{
    color: rgba(255, 255, 0, 0.77);
}



.skillBars{
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;

}

.skillBar{
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background-color: rgb(50, 50, 50);
}

.skillBars span{
    color: rgb(60, 255, 0);
    text-decoration: none;
}

.skillBars a {
    color: rgb(0, 85, 255);
}

.skillBarImg{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right:2rem;
}

.skillBarText p{
    font-size: 0.8rem;
    font-weight: 200;

}

.skillBars a{
    color: aqua;
    text-decoration: none;
}



/*==== tablet ====*/
@media screen and (max-width:740px){
    .skillBarText h2{
        font-size: 0.8rem;
    }

    .skillBarText p{
        font-size: 0.8rem;
    }

    .skillBar{
        display: flex;
        flex-direction: column;
        align-items: center;
        
    }

    .skillBar img{
        padding-bottom: 1rem;
    }

    

}
